import {React, useState, useEffect} from 'react';
import Navbar from './Navbar'
import PostTeacher from './PostTeacher'
import ExcellentTeachersStyles from '../Styles/ExxcelentTeachersStyles.css';
import {Paper, Button, Stack } from '@mui/material';
import wide from '../Media/wideC.jpg';
import oblicej from '../Media/hans.jpg';
import useFetch3 from "../Requester/useFetch3";
import domain from '../Content/domain.json'
import Footer from '../Content/Footer';

const ExcellentTeachers = (url) => {
  const [faculty, setFaculty] = useState(['PrF', 'Prodovdeck fakulta']);
  const {data3, setData3} = useFetch3();
  useEffect(() => {
    let m = domain.domain+`/teachers/api`;
          setData3({ ...data3, slug: m });    
setTimeout(()=>{
    }, 100)
  
  }, [faculty])
return(<div>
  <img style={{opacity: '1', position: 'absolute', zIndex: '-5'}} src={wide}></img>
<Navbar/><br></br>
<Paper sx={{width: '35%', marginLeft: '58%', marginTop: '180px', height:'fit-content', opacity: '0.7'}}>
  <h2 style={{paddingLeft: '25px',paddingTop: '25px', paddingBottom: '-10px'}}>EXCELENTN UITEL</h2>
  <p style={{padding: '25px', textAlign: 'justify'}}>Program excelentnch uitel je iniciativa univerzity, kter si klade za cl odmovat nejlep pedagogy na zklad kvality jejich vuky a spch student. Tento program je zaloen na pravidelnm hodnocen uitel studenty a dalmi kolegy, kte posuzuj pedagogick dovednosti a schopnosti uitele.

Kad rok je zvolen jeden uitel, kter zsk titul excelentnho uitele roku a tak finann odmnu. Tato ocenn jsou udlovna na zklad mnoha kritri, jako je napklad pstup k studentm, schopnost pedvn znalost, zapojen student do vuky, inovativn pstup k vuce a mnoho dalch.

Excelentn uitel jsou vzorem pro ostatn uitele na univerzit a pomhaj vytvet pozitivn a podprn prosted pro studenty. Program excelentnch uitel tak podporuje uitele v jejich dalm profesnm rstu a rozvoji, a tm pispv k neustlmu zlepovn vuky na univerzit.</p>
</Paper>
<Button sx={{width: '35%', marginLeft: '58%', height:'fit-content', marginBottom: '180px'}} variant="contained"
 onClick={() => window.scroll({top: 1000, behavior: 'smooth'})}
>Pejt na nejlep uitele</Button>
{!data3.results ? "Loading..." : data3.results.map(({ _id, caption, name, faculty, date, imageURL}) =>         
<PostTeacher imageURL={imageURL} caption={caption} name={name}/>)}
<Footer></Footer>
</div>);
}
export default ExcellentTeachers;